import GisEvent from "./GisEvent";
import DeveloperError from "./DeveloperError";
/**
 * 这个是所有Ex图层的接口,所有Ex图层都要继承这个接口。
 * @alias ILayerEX
 **/
class ILayerEX {
    /**
    * @constructor
    * @param {Viewer} viewer viewer对象
    * @param {option}  option 传入入的参数
    */
    constructor(viewer,option) {
        this._show = false;
        /**
         * 数据Promise
         * @readonly
         * @type {Promise}
         * @default undefined
        */
        this.featureReadyPromise=undefined;
         /**
         * 图层是否打开
         * @type {Object}
         * @default undefined
        */
        this.visible=fasle;

        /**
         * 图层所有要素
         * @readonly
         * @type {Dictionary}
        */
        this.features=undefined;

        /**
         * 图层名称
         * @readonly
         * @type {String}
        */
        this.name=undefined;

        /**
         * 图层id
         * @type {String}
        */
        this.id=undefined;

        /**
         * 图层id
         * @readonly
         * @type {Viewer}
        */
        this.viewer=undefined;

        /**
         * 图层全局渲染器
         * @readonly
         * @type {Object}
        */
        this.symbol=undefined;

         /**
         * 图层透明度 取值范围为[0-1]
         * @type {Number}
        */
        this.alpha=1;

        /**
         * 子图层
         * @type {Array<ILayerEX>}
         */
        this.children=[];

        /**
         * 获取所属对象
         */
        this.owner=undefined;

        /**
         * 图层事件
         * @type {GisEvent}
         */
        this.event=new GisEvent();
        
    }
    /**
     * 图层显隐控制
     * @type {Boolean}
     * @default false
    */
    get show() {
		return this._show;
	}
	set show(value) {
		this._show=value;
	}
  
    /**
     * 加载要素，增加或者删除
     * @param {Array|Object} data 对象或者对象数据
     */
    load(data) {
        throw new DeveloperError('error');
    }
     /**
     * 加载要素，本质调用的load方法
     * @param {Array|Object} data 对象或者对象数据
     */
    add(data) {
        throw new DeveloperError('error');
    }

    /**
     * 根据id获取要素
     * @param {String} id 要素id
     * @return {Feature} 要素
     */
    getById(id) {
        throw new DeveloperError('error');
    }

    /**
     * 根据id删除要素
     * @param {String} id 要素id
     * @return {Boolean} 删除成功返回true,否则返回false
     */
    removeById(id) {
        throw new DeveloperError('error');
    }

    /**
     * 删除所有要素
     * @return {Boolean} 删除成功返回true,否则返回false
     */
    removeAll() {  
        throw new DeveloperError('error');
    }
    /**
     * 定位要素
     * @param {String} id 要素id
     */
    zoomById(id,option={}) {
        throw new DeveloperError('error');
    }

    /**
     * 高亮要素
     * @param {String} id 要素id
     */
    highLightById(id,option={}) {
        throw new DeveloperError('error');
    }

    /**
     * 定位并高亮要素
     * @param {String} id 要素id
    */
    zoomAndHLById(id,option={}) {
        throw new DeveloperError('error');
    }
    /**
     * 销毁图层
     * @return {boolean} 销毁成功返回true,否则返回false
     */
    destroy() {
        throw new DeveloperError('error');
    }
}
export default ILayerEX;