/**
 * 通用事件
 * @alias GisEvent
 * @constructor
 */
class GisEvent {
    constructor() {
        this.events = {};
    }

    /**
     * 添加事件
     * @param {String}  type 事件类型
     * @return {Function}  fun  事件函数
     */
    add(type, fun) {
        if(!this.events[type]){
            this.events[type]=[];
        }
        this.events[type].push(fun);
    }

    /**
     * 触发事件
     * @param {String}  type 事件类型
     * @param {Object}  parm  事件参数
     */
    raise(type, parm) {

        var ev = this.events[type];
        if(ev instanceof Array){
            for (let f of ev) {
                if (f.innerId) {
                    f.fun(parm);
                } else {
                    f(parm);
                }
    
            }
        }
       
    }

   /**
    * 删除事件
    * @param {String}  type 事件类型
    * @param {Function}  fun  事件函数
    */
    remove(type, fun) {
        var eves = this.events[type];
        var index = -1;
        for (var i = 0; i < eves.length; i++) {
            if (eves[i] == fun) {
                index = i;
                break;
            } else if (eves[i].innerId && fun.innerId && eves[i].innerId == fun.innerId) {
                index = i;
                break;
            }
        }
        eves = eves.splice(i, 1); //删除掉
    }
    /**
     * 删除所有事件
     */
    removeAll() {
        for (var prop in this.events) {
            this.events[prop] = [];
        }
        this.events={};
    }
    /**
     * 事件绑定
     * @param {String}  type 事件类型
     * @param {Function}  fun  事件函数
     */
    addEvent(type,fun){
        this.add(type,fun);
    }
    
    /**
     * 触发事件
     * @param {String}  type 事件类型
     * @param {Object}  parm  事件参数
     */
    raiseEvent(type,parm){
        this.raise(type,parm);
    }
    
    /**
     * 删除事件
     * @param {String}  type 事件类型
     * @param {Function}  fun  事件函数
     */
    removeEvent(type,fun){
        this.remove(type,fun)
    }
    /**
     * 删除所有事件
     */
    removeAllEventfunction(){
       this.removeAll();
    }

}


export default GisEvent;